.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.search-music{
  background: transparent;
  border: 0;
  display: inline-block;
  font-size: 1em;
  height: 100%;
  left: 0;
  margin: 0;
  outline: none;
  padding: 2px 6px;
  position: relative;
  width: 100%;
  color: white;
}

.search-music::-webkit-input-placeholder{
  color: white;
}

.search-music:-ms-input-placeholder{
  color: white;
}

.search-music::placeholder{
  color: white;
}

.artistas-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.artistas-name{
  list-style-type: none;
  font-size: 1em;
  color: black;

}

.artistas__button{
  margin-left: 10px;
}

.music-container{
 width: 100%;
}



.video-container{
  margin-top: 50px;
 text-align: center;
  
}

.video-list{
  float: left;
  width: 33%;
}

.musica-copy{
  color: black;
  font-size: 20px;
}

.favoritos{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: left;
      align-items: left;
  margin-bottom: 30px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



